<template>
  <div id="app">
    <div class="container">
      <movie-list :movies="movies" :genres="genres"></movie-list>
    </div>
  </div>
</template>

<script>
import MovieList from './components/movies/MovieList.vue'

const axios = require('axios');

export default {
  name: 'app',
  components: {
    MovieList,
  },
  data() {
    return {
      movies: [],
      genres: [],
    }
  },
  created() {
    axios.get('https://gist.githubusercontent.com/edutak/edcf74faba5582db62734260a0e28e2c/raw/51913379cc9e898c480e64a47c9f5bc75eca3289/movie.json')
      .then(response => {
        this.movies = response.data
      })
      .catch(error => error)
    axios.get('https://gist.githubusercontent.com/edutak/9059d16c698a6c59b4e9ec9636595b78/raw/ddea711da71d3d8695b0fd0408bc1637add91ee0/genre.json')
      .then(response => {
        this.genres = [ 
          {id: 0, name: '전체보기'},
          ...response.data
        ]
      })
  },
}
</script>

